"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { createClient } from "@/lib/supabase/client"
import { Plus, Edit2, Trash2, Search, User, Mail, Phone, Briefcase } from "lucide-react"

type StaffRole = 'manager'

export default function StaffPage() {
  const router = useRouter()
  const supabase = createClient()
  const [loading, setLoading] = useState(true)
  const [staff, setStaff] = useState<any[]>([])
  const [filteredStaff, setFilteredStaff] = useState<any[]>([])
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedRole, setSelectedRole] = useState<string>('all')
  const [showModal, setShowModal] = useState(false)
  const [editingStaff, setEditingStaff] = useState<any>(null)
  const [restaurant, setRestaurant] = useState<any>(null)
  const [availableRoles, setAvailableRoles] = useState<any[]>([])
  
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    role: '',
    pin: ''
  })

  useEffect(() => {
    loadStaff()
    loadRoles()
  }, [])

  useEffect(() => {
    filterStaff()
  }, [searchTerm, selectedRole, staff])

  async function loadRoles() {
    try {
      const selectedRestaurantId = localStorage.getItem('selected_restaurant_id')
      if (!selectedRestaurantId) return

      // Load unique roles from staff table
      const { data: staffData } = await supabase
        .from('staff')
        .select('role')
        .eq('restaurant_id', selectedRestaurantId)

      if (staffData) {
        // Get unique roles
        const uniqueRoles = [...new Set(staffData.map(s => s.role).filter(Boolean))]
        setAvailableRoles(uniqueRoles.map(role => ({ value: role, label: role })))
      }
    } catch (error) {
      console.error('Error loading roles:', error)
    }
  }

  async function loadStaff() {
    setLoading(true)
    try {
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) {
        router.push('/login')
        return
      }

      // Get selected restaurant from localStorage
      const selectedRestaurantId = localStorage.getItem('selected_restaurant_id')
      console.log('Selected restaurant ID:', selectedRestaurantId)
      
      if (!selectedRestaurantId) {
        router.push('/dashboard')
        return
      }

      const { data: restaurants, error: restaurantError } = await supabase
        .from('restaurants')
        .select('*')
        .eq('id', selectedRestaurantId)
        .eq('owner_id', user.id)
        .single()

      console.log('Restaurant:', restaurants, 'Error:', restaurantError)

      if (!restaurants) return
      setRestaurant(restaurants)

      const { data: staffData, error: staffError } = await supabase
        .from('staff')
        .select('*')
        .eq('restaurant_id', restaurants.id)
        .order('name')

      console.log('Staff data:', staffData, 'Error:', staffError)
      console.log('Staff count:', staffData?.length)

      if (staffData) {
        setStaff(staffData)
      } else {
        setStaff([])
      }
    } catch (error) {
      console.error('Error loading staff:', error)
      setStaff([])
    } finally {
      setLoading(false)
    }
  }

  function filterStaff() {
    let filtered = staff

    if (selectedRole !== 'all') {
      filtered = filtered.filter(s => s.role === selectedRole)
    }

    if (searchTerm) {
      filtered = filtered.filter(s =>
        s.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        s.email?.toLowerCase().includes(searchTerm.toLowerCase())
      )
    }

    setFilteredStaff(filtered)
  }

  function openModal(staffMember: any = null) {
    if (staffMember) {
      setEditingStaff(staffMember)
      setFormData({
        name: staffMember.name || '',
        email: staffMember.email || '',
        phone: staffMember.phone || '',
        role: staffMember.role || '',
        pin: ''
      })
    } else {
      setEditingStaff(null)
      setFormData({
        name: '',
        email: '',
        phone: '',
        role: '',
        pin: ''
      })
    }
    setShowModal(true)
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault()
    
    if (!restaurant) return

    const staffData = {
      name: formData.name,
      email: formData.email,
      phone: formData.phone,
      role: formData.role,
      restaurant_id: restaurant.id,
      ...(formData.pin && { pin: formData.pin })
    }

    try {
      if (editingStaff) {
        await supabase
          .from('staff')
          .update(staffData)
          .eq('id', editingStaff.id)
      } else {
        await supabase
          .from('staff')
          .insert(staffData)
      }

      setShowModal(false)
      loadStaff()
      loadRoles() // Refresh roles list
    } catch (error) {
      console.error('Error saving staff:', error)
    }
  }

  async function deleteStaff(id: string) {
    if (!confirm('Hapus staff ini?')) return

    try {
      await supabase
        .from('staff')
        .delete()
        .eq('id', id)

      loadStaff()
    } catch (error) {
      console.error('Error deleting staff:', error)
    }
  }

  const getRoleLabel = (role: string) => {
    return role || 'Tidak ada peran'
  }

  const getRoleColor = (role: string) => {
    if (role === 'manager') return 'bg-purple-100 text-purple-800 border-purple-200'
    return 'bg-blue-100 text-blue-800 border-blue-200'
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[var(--color-accent)]"></div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white border-b border-gray-200 shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-bold text-gray-900">Kelola Staff</h1>
              <p className="text-sm text-gray-500 mt-1">Kelola akun dan peran staff</p>
            </div>
            <button
              onClick={() => openModal()}
              className="flex items-center gap-2 px-4 py-2 bg-[var(--color-accent)] text-white rounded-lg font-medium hover:bg-[var(--color-accent-hover)] transition-colors"
            >
              <Plus className="w-5 h-5" />
              Tambah Staff
            </button>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Search and Filter */}
        <div className="bg-white rounded-2xl p-4 mb-6 border border-gray-100 shadow-sm">
          <div className="flex flex-col md:flex-row gap-4">
            <div className="flex-1 relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
              <input
                type="text"
                placeholder="Cari staff..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[var(--color-accent)]"
              />
            </div>
            <select
              value={selectedRole}
              onChange={(e) => setSelectedRole(e.target.value)}
              className="px-4 py-2.5 border-2 border-gray-200 rounded-xl font-medium focus:outline-none focus:border-gray-400 hover:border-gray-300 transition-all cursor-pointer"
            >
              <option value="all">Semua Peran</option>
              {availableRoles.map(role => (
                <option key={role.value} value={role.value}>{role.label}</option>
              ))}
            </select>
          </div>
        </div>

        {/* Staff Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredStaff.length === 0 ? (
            <div className="col-span-full bg-white rounded-2xl p-12 text-center border border-gray-100">
              <User className="w-16 h-16 text-gray-300 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-900 mb-2">
                {staff.length === 0 ? 'Belum Ada Staff' : 'Tidak ada staff ditemukan'}
              </h3>
              <p className="text-gray-500 mb-6">
                {staff.length === 0 
                  ? 'Tambahkan staff pertama untuk mulai mengelola tim Anda'
                  : 'Coba ubah filter atau kata kunci pencarian'}
              </p>
              {staff.length === 0 && (
                <button
                  onClick={() => openModal()}
                  className="inline-flex items-center gap-2 px-6 py-3 bg-[var(--color-accent)] text-white rounded-xl font-semibold hover:bg-[var(--color-accent-hover)] transition-colors"
                >
                  <Plus className="w-5 h-5" />
                  Tambah Staff Pertama
                </button>
              )}
            </div>
          ) : (
            filteredStaff.map((member) => (
              <div key={member.id} className="bg-white rounded-2xl p-6 border border-gray-100 shadow-sm hover:shadow-md transition-shadow">
                <div className="flex items-center gap-4 mb-4">
                  <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center">
                    <User className="w-8 h-8 text-gray-400" />
                  </div>
                  <div className="flex-1">
                    <h3 className="font-bold text-lg text-gray-900">{member.name}</h3>
                    <span className={`inline-block px-2 py-1 rounded-full text-xs font-semibold border ${getRoleColor(member.role)}`}>
                      {getRoleLabel(member.role)}
                    </span>
                  </div>
                </div>
                
                <div className="space-y-2 mb-4">
                  <div className="flex items-center gap-2 text-sm text-gray-600">
                    <Mail className="w-4 h-4" />
                    <span>{member.email || '-'}</span>
                  </div>
                  <div className="flex items-center gap-2 text-sm text-gray-600">
                    <Phone className="w-4 h-4" />
                    <span>{member.phone || '-'}</span>
                  </div>
                </div>

                <div className="flex gap-2">
                  <button
                    onClick={() => openModal(member)}
                    className="flex-1 flex items-center justify-center gap-2 px-4 py-2 bg-blue-500 text-white rounded-lg font-medium hover:bg-blue-600 transition-colors"
                  >
                    <Edit2 className="w-4 h-4" />
                    Edit
                  </button>
                  <button
                    onClick={() => deleteStaff(member.id)}
                    className="px-4 py-2 bg-red-500 text-white rounded-lg font-medium hover:bg-red-600 transition-colors"
                  >
                    <Trash2 className="w-4 h-4" />
                  </button>
                </div>
              </div>
            ))
          )}
        </div>
      </main>

      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-2xl p-6 max-w-md w-full">
            <h2 className="text-2xl font-bold mb-6">
              {editingStaff ? 'Edit Staff' : 'Tambah Staff'}
            </h2>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Nama Lengkap
                </label>
                <input
                  type="text"
                  required
                  value={formData.name}
                  onChange={(e) => setFormData({...formData, name: e.target.value})}
                  className="w-full px-4 py-2.5 border-2 border-gray-200 rounded-xl font-medium focus:outline-none focus:border-gray-400 hover:border-gray-300 transition-all"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Email
                </label>
                <input
                  type="email"
                  required
                  value={formData.email}
                  onChange={(e) => setFormData({...formData, email: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Telepon
                </label>
                <input
                  type="tel"
                  value={formData.phone}
                  onChange={(e) => setFormData({...formData, phone: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Peran
                </label>
                <input
                  type="text"
                  required
                  value={formData.role}
                  onChange={(e) => setFormData({...formData, role: e.target.value})}
                  list="role-suggestions"
                  placeholder="Contoh: manager, kang kopi, pembuat snack"
                  className="w-full px-4 py-2.5 border-2 border-gray-200 rounded-xl font-medium focus:outline-none focus:border-gray-400 hover:border-gray-300 transition-all"
                />
                <datalist id="role-suggestions">
                  {availableRoles.map(role => (
                    <option key={role.value} value={role.value} />
                  ))}
                </datalist>
                {availableRoles.length > 0 && (
                  <p className="mt-2 text-xs text-gray-500">
                    Peran yang sudah ada: {availableRoles.map(r => r.label).join(', ')}
                  </p>
                )}
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  PIN (4 digit)
                </label>
                <input
                  type="password"
                  maxLength={4}
                  pattern="[0-9]{4}"
                  placeholder={editingStaff ? 'Kosongkan jika tidak diubah' : ''}
                  required={!editingStaff}
                  value={formData.pin}
                  onChange={(e) => setFormData({...formData, pin: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                />
              </div>
              <div className="flex gap-2 pt-4">
                <button
                  type="button"
                  onClick={() => setShowModal(false)}
                  className="flex-1 px-4 py-2 border border-gray-200 text-gray-700 rounded-lg font-medium hover:bg-gray-50 transition-colors"
                >
                  Batal
                </button>
                <button
                  type="submit"
                  className="flex-1 px-4 py-2 bg-[var(--color-accent)] text-white rounded-lg font-medium hover:bg-[var(--color-accent-hover)] transition-colors"
                >
                  {editingStaff ? 'Simpan' : 'Tambah'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  )
}
